// verticle column chart
.column-chart {
  position: relative;
  z-index: 20;
  bottom: 0;
  left: 50%;
  width: 100%;
  height: 320px;
  height: rem(320px);
  margin-top: 40px;
  margin-top: rem(40px);
  margin-left: -50%;
  &:before,
  &:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: calc(100% + 30px);
    height: 25%;
    margin-left: -15px;
    margin-left: rem(-15px);
    border-top: $border-lightgray-dashed-1;
    border-bottom: $border-lightgray-dashed-1;
  }
}
.column-chart:after {
  top: 50%;
}

.column-chart>.legend {
  position: absolute;
  z-index: -1;
  top: 0;
}

.column-chart>.legend.legend-left {
  left: 0;
  width: 25px;
  width: rem(25px);
  height: 75%;
  margin-left: -55px;
  margin-left: rem(-55px);
  border: $border-lightgray-dashed-1;
  border-right: none;
}

.column-chart>.legend.legend-left>.legend-title {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 65px;
  width: rem(65px);
  height: 50px;
  height: rem(50px);
  line-height: 50px;
  line-height: rem(50px);
  margin-top: -25px;
  margin-left: -60px;
  font-size: 28px;
  font-size: rem(28px);
  letter-spacing: 1px;
}

.column-chart>.legend.legend-right {
  right: 0;
  width: 100px;
  width: rem(100px);
  height: 100%;
  margin-right: -115px;
  margin-right: rem(-115px);
  z-index: 100;
}

.column-chart>.legend.legend-right>.item {
  position: relative;
  width: 1%;
  height: 25%;
}

.column-chart>.legend.legend-right>.item>h4 {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  height: 30px;
  line-height: 32px;
  line-height: rem(32px);
  margin-top: -20px;
  margin-top: rem(-20px);
  font-size: 16px;
  font-size: rem(16px);
  text-align: right;
  background: $blue;
  border: $border-lightblue-solid-1;
  border-radius: 20px;
  padding: 0 20px;
  box-shadow: 3px 4px 2px rgba(0, 0, 0, 0.20);
}

.column-chart>.chart {
  position: relative;
  z-index: 20;
  bottom: 0;
  left: 50%;
  width: 98%;
  height: 100%;
  margin-left: -49%;
}

.column-chart>.chart>.item {
  position: relative;
  float: left;
  height: 100%;
}

.column-chart>.chart>.item:before {
  position: absolute;
  z-index: -1;
  content: '';
  bottom: 0;
  left: 50%;
  width: 1px;
  height: calc(100% + 15px);
  border-right: $border-lightgray-dashed-1;
}

.column-chart>.chart>.item>.bar {
  position: absolute;
  bottom: 0;
  left: 3px;
  left: rem(3px);
  width: 94%;
  height: 100%;
}

.column-chart>.chart>.item>.bar>span.percent {
  display: block;
  position: absolute;
  z-index: 25;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 26px;
  line-height: 26px;
  color: $white;
  background-color: $red-5;
  font-size: 14px;
  font-size: rem(14px);
  font-weight: 700;
  text-align: center;
  letter-spacing: 1px;
}

.column-chart>.chart>.item>.bar>.item-progress {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  color: $white;
  background-color: $blue-475;
}

.column-chart>.chart>.item>.bar>.item-progress>.title {
  position: absolute;
  top: calc(50% - 13px);
  left: 50%;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  letter-spacing: rem(2px);
  // -moz-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
  // -o-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
  // -ms-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
  // -webkit-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
  transform: translateX(-50%) translateY(-50%) rotate(-90deg);
}

@media (min-width: 360px) {
  .column-chart>.chart>.item>.bar>.item-progress>.title {
      font-size: 16px;
      font-size: 1rem;
  }
}

@media (min-width: 480px) {
  .column-chart>.chart>.item>.bar>.item-progress>.title {
      font-size: 18px;
      font-size: rem(18px);
  }
}

// player age group chart - horizontal progress bars
.age-chart {
  font-size: 11px;


  .prog-bar {
    height: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: $gray-100;
    border-radius: 4px;
    @include box-shadow-custom-inset(0, -1px, 0, 0, rgba(0, 0, 0, .15));
  }

  .prog-bar-lightblue-success {
    background-color: $green-10;
  }

  .prog-bar-lightblue {
    float: left;
    width: 0;
    height: 100%;
    font-size: 12px;
    font-size: rem(12px);
    line-height: 20px;
    color: $white;
    text-align: center;
    background-color: $blue-450;
    @include animateWidth(1px, 45%);
    @include box-shadow-custom-inset(0, -1px, 0, 0, rgba(0, 0, 0, .15));
    transition: width .6s ease;
  }
}